body {
  height: 100%;
  font-family: sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.nav-sh{
   
  box-shadow: 0px 5px 8px #5F5F5F;
  background-color: black;
    
}

.date-x{
    /* padding: 20px; */
    float: right;
    font-size: 20px;
}



@keyframes slidein {
    from {
      margin-left: 100%;
      width: 200%; 
    }
  
    to {
      margin-left: 0%;
      width: 100%;
    }
  }
.disp{
    
    padding-top: 40px;
    display: flex;
    justify-self: center;
    align-items: center;
    position: relative;
}

.c1{
    animation-duration: 2s;
    animation-name: slidein;
}
.c2{
    animation-duration: 2s;
    animation-name: slidein;
}
.c3{
    animation-duration: 2s;
    animation-name: slidein;
}




.table-main{
    
    margin-top: 50px;
    
    
}


.load{
  height: 90vh;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 200px;
}


.def{
  padding: 20px;
  
}

.store-st{
  padding-top: 40px;
}

.card-store{
  box-shadow: 0px 8px 18px #5F5F5F;
}



.pad-table{
  padding-bottom: 40px;
}



.main {
	font: 16px / 1 Arial;
	position: relative;
}


.smooth-scroll {
	padding: 50px;

	&--spacer {
		border: 1px dashed #fff;
		height: 2000px;
	}

	button {
		color: #000;
		font: bold 18px/1 Arial;
		margin: 10px 0;
		padding: 10px;
		width: 100%;
	}
}